.CodeMirror-hints {
  z-index: 10;
  max-height: 20em;
  background: #fff;
  border: 1px solid silver;
  border-radius: 3px;
  margin: 0;
  padding: 2px;
  font-family: monospace;
  font-size: 90%;
  list-style: none;
  position: absolute;
  overflow-x: hidden;
  overflow-y: auto;
  box-shadow: 2px 3px 5px #0003;
}

.CodeMirror-hint {
  max-width: 19em;
  white-space: pre;
  color: #000;
  cursor: pointer;
  border-radius: 2px;
  margin: 0;
  padding: 0 4px;
  overflow: hidden;
}

li.CodeMirror-hint-active {
  color: #fff;
  background: #08f;
}

